.Toolbar {
  height: 45px;
  width: 100%;
  background-color: #703b09;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  z-index: 90;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Toolbar nav {
  height: 100%;
}

.Logo {
  height: 100%;
}

@media (max-width: 478px) {
  .DesktopOnly {
    display: none;
  }
}



